<template>
    <div>
        <h1>別想了，今晚吃{{ targetData.name }}吧！</h1>
        <h3 v-if="targetData.alias">就是{{ targetData.alias }}拉</h3>
        <ul>
            <li><button @click="setRandom">我要重選</button></li>
            <li>
                <select v-model="random">
                    <option
                        v-for="(item, index) in dinnerList"
                        :value="index" :key="index"
                    >{{ item[0] }}</option>
                </select>
            </li>
            <li v-if="targetData.address">
                <a :href="'https://maps.google.com/?q='+ targetData.address" target="_blank">地址: {{ targetData.address }}</a>
            </li>
            <li v-if="targetData.telephoneNumber">
                <a :href="'tel:'+ targetData.telephoneNumber" target="_blank">電話: {{ targetData.telephoneNumber }}</a>
            </li>
        </ul>
        <img v-if="targetData.menu" :src="targetData.menu" alt="菜單">
    </div>
</template>

<script>

    export default {
        data: function(){

            return {
                dinnerList: [],
                random: null,
            }
        },
        computed: {
            targetData: function(){
                let $result = null;
                
                let $dinnerList = this.dinnerList,
                    _random = this.random;

                let $target = $dinnerList[_random]

                if( $target ) {

                    $result = {
                        name: $target[0],
                        alias: $target[1],
                        address: $target[2],
                        telephoneNumber: $target[3],
                        menu: $target[4],
                    }
                }

                return $result;
            }
        },
        methods: {
            setRandom: function(){
                let $dinnerList = this.dinnerList,
                    _length = $dinnerList.length;

                let _now = this.random,
                    _result = Math.floor(Math.random() * _length);

                if( _now !== _result ) {
                    this.$set(this, 'random', _result);
                }else {
                    this.setRandom();
                }
            },
            initData: function(res){
                let $data = res.data;

                this.$set(this, 'dinnerList', $data);

                this.setRandom();
            },
            getData: function(){
                let _url = 'https://script.google.com/macros/s/AKfycbza0u6xP7rpEhD0NgOVeJlu5nrxiPV0CktKtfan74sKU35pHzrw/exec';

                return this.$http.get(_url);
            },
            init: async function(){

                try {
                    let $response = await Promise.all([
                            this.getData(),
                        ]);

                    let [data] = $response;
                    
                    this.initData(data);

                }catch(error) {
                    console.errror(error)
                }
            }
        },
        created: function(){
            this.init();
        }
    }
</script>

<style scoped lang="scss">

    li {

        + li {
            margin-top: 10px;
        }
    }
</style>
